import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Group, Div, Avatar, PanelHeader, Cell, Counter, List } from '@vkontakte/vkui';
import Icon24Like from '@vkontakte/icons/dist/24/like';

const Home = ({ id, go, fetchedUser, user }) => (
	<Panel id={id}>
		<PanelHeader>Конкурс на активность</PanelHeader>
		{fetchedUser &&
		<Group title="Общая информация">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200} size={80}/> : null}
				description={"Участник конкурса"}>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

		{user &&
		<Group title="Количество баллов">	
				<List>
					<Cell before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Лайки</Cell>
					<Cell before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Лайки первым</Cell>
					<Cell before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Лайки в числе первых</Cell>
					<Cell before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Лайки всех постов</Cell>
					<Cell before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Хорошие посты</Cell>
					<Cell before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Хорошие комментарии</Cell>
				</List>					
		</Group>}
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
